@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,700&display=swap);
.article{padding:30px;background:#fff;max-width:500px;border-radius:10px;box-shadow:1px 1px 20px rgba(0,0,0,0.3)}.article__date{color:#999;font-size:12px}.article__title{font-size:32px;letter-spacing:0.5px;color:#333;margin-bottom:20px;margin-top:10px}.article__text{font-size:18px;color:#333}

*{font-family:'Roboto', sans-serif;font-size:12px;padding:0;margin:0;box-sizing:border-box}.container{height:100vh;display:flex;justify-content:center;align-items:center;background:#1488cc;background:linear-gradient(to right, #2b32b2, #1488cc)}

